:host .search {
  display: flex;
  align-items: center;
  flex-direction: row;
  box-sizing: border-box;
  padding-left: var(--mdx-comp-searchbar-padding-vertical-default);
  padding-right: var(--mdx-comp-searchbar-padding-vertical-default);
  width: var(--mdx-comp-searchbar-sizing-width-desktop);
  height: var(--mdx-comp-searchbar-sizing-height);
  background-color: var(--mdx-comp-searchbar-background-color-default);
  border-radius: var(--mdx-comp-searchbar-border-radius-default);
  box-shadow: var(--mdx-comp-searchbar-box-shadow-default);
  border: var(--mdx-comp-searchbar-border-width-default) solid var(--mdx-comp-searchbar-border-color-default);

  @media screen and (max-width: 768px) {
    width: var(--mdx-comp-searchbar-sizing-width-mobile);
  }

  &__button {
    all: unset;
    cursor: pointer;
    height: min-content;
    transition-duration: var(--mdx-comp-searchbar-transition-fade-icon-in-out-duration);
    transition-delay: var(--mdx-comp-searchbar-transition-fade-icon-in-out-delay);
    transition-timing-function: var(--mdx-comp-searchbar-transition-fade-icon-in-out-timing-function);
    transition-property: opacity, width, height;

    &:focus-visible {
      z-index: 10;
      border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
      outline-color: var(--mdx-comp-keyboard-focus-color-default);
      outline-style: solid;
      outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
      outline-offset: 2px;
    }

    &--hidden {
      opacity: 0;
      width: 0;
      height: 0;
    }
  }

  &__flyout {
    position: absolute;
    padding: 5px 0 0 5px;
    width: var(--mdx-comp-searchbar-sizing-width-desktop);

    @media screen and (max-width: 768px) {
      width: var(--mdx-comp-searchbar-sizing-width-mobile);
    }
  }

  &:focus-within {
    background-color: var(--mdx-comp-searchbar-background-color-focus);
    box-shadow: var(--mdx-comp-searchbar-box-shadow-focus);
    border-color: var(--mdx-comp-searchbar-border-color-focus);
  }

  &__icon-search {
    color: var(--mdx-comp-searchbar-search-button-color-default);

    &:hover {
      color: var(--mdx-comp-searchbar-search-button-color-hover);
    }
  }

  &__input {
    width: 100%;
    border: none;
    outline: none;
    background-color: inherit;
    padding-left: var(--mdx-comp-searchbar-padding-vertical-default);
    caret-color: var(--mdx-comp-searchbar-border-color-focus);
    color: var(--mdx-comp-searchbar-input-color-default);
    font: var(--mdx-comp-searchbar-font-default);
    letter-spacing: var(--mdx-comp-searchbar-font-default-letter-spacing);
    text-transform: var(--mdx-comp-searchbar-font-default-text-case);
    text-decoration: var(--mdx-comp-searchbar-font-default-text-decoration);

    &::placeholder {
      color: var(--mdx-comp-searchbar-placeholder-color-default);
    }

    &:focus-within {
      color: var(--mdx-comp-searchbar-input-color-focus);
    }
  }

  &__icon-x {
    margin-left: auto;
    color: var(--mdx-comp-searchbar-clear-button-color-default);

    &:hover {
      color: var(--mdx-comp-searchbar-clear-button-color-hover);
    }
  }
}

:host([disabled]) .search {
  background-color: var(--mdx-comp-searchbar-background-color-disabled);
  border-color: var(--mdx-comp-searchbar-border-color-disabled);

  &__button {
    cursor: default;
  }

  &__input {
    color: var(--mdx-comp-searchbar-input-color-disabled);

    &::placeholder {
      color: var(--mdx-comp-searchbar-placeholder-color-disabled);
    }
  }

  &__icon-search {
    color: var(--mdx-comp-searchbar-search-button-color-disabled);
  }

  &__icon-x {
    color: var(--mdx-comp-searchbar-clear-button-color-disabled);
  }
}
