import { Component, Element, Event, EventEmitter, h, Host, Prop, State } from '@stencil/core';
import { parseAttribute } from '../../utils';
import { ICON_SIZES } from '../icons/exports';
import { SuggestionList } from '../input/input';

/**
 * @slot - For Label and Icon
 */
@Component({
  tag: 'mdx-search-input',
  styleUrl: 'search.scss',
  shadow: true,
})
export class SearchInput {
  @Element()
  host: HTMLMdxSearchInputElement;

  @Prop()
  disabled = false;

  @Prop()
  placeholder: string;

  @Prop()
  value = '';

  @Prop()
  suggestionList?: SuggestionList;

  @Prop()
  searchButtonAriaLabel!: string;

  @Prop()
  resetButtonAriaLabel!: string;

  @Prop()
  selectedItem: HTMLMdxFlyoutItemElement['item'];

  @Event()
  searchTrigger: EventEmitter<{ value: string }>;

  @State()
  matchedInput: { label: string }[] = [];

  private handleInputChange = (event: Event) => {
    this.value = (event.target as HTMLInputElement).value;

    const matchedItems = this.suggestionList?.filter((item) => item.label.toLowerCase().includes(this.value.toLowerCase()));
    this.matchedInput = matchedItems || [];
  };

  private select(item: string) {
    if (this.selectedItem === item) {
      return false;
    }
    this.selectedItem = item;

    return (this.value = this.selectedItem);
  }

  connectedCallback() {
    this.suggestionList = parseAttribute(this.host, 'suggestion-list', this.suggestionList);
  }

  render() {
    return (
      <Host>
        <div class="search">
          <button
            class="search__button"
            aria-label={this.searchButtonAriaLabel}
            onClick={() => this.searchTrigger.emit({ value: this.value })}
            type="submit"
          >
            <mdx-icon-search size={ICON_SIZES.SM} class="search__icon-search" />
          </button>
          <input
            type="text"
            class="search__input"
            value={this.value}
            spellcheck={false}
            disabled={this.disabled}
            placeholder={this.placeholder}
            onInput={(event) => this.handleInputChange(event)}
            onKeyUp={(event) => {
              if (event.key === 'Enter') {
                this.searchTrigger.emit({ value: this.value });
              }
            }}
          />
          <button
            class={`search__button${this.value === '' ? ' search__button--hidden' : ''}`}
            aria-label={this.resetButtonAriaLabel}
            type="reset"
            onClick={() => {
              this.value = '';
              const input = this.host.shadowRoot?.querySelector('input');
              input?.focus();
            }}
          >
            <mdx-icon-x size={ICON_SIZES.SM} class="search__icon-x" />
          </button>
        </div>
        {this.value && this.suggestionList && this.suggestionList.length > 0 && (
          <div class="search__flyout">
            {this.matchedInput.length > 0 && (
              <mdx-flyout>
                {this.matchedInput.map((element) => (
                  <mdx-flyout-item
                    item={element.label}
                    label={element.label}
                    onItemClick={({ detail: { item } }) => {
                      this.select(item);
                      this.matchedInput = [];
                      this.searchTrigger.emit({ value: this.value });
                    }}
                  />
                ))}
              </mdx-flyout>
            )}
          </div>
        )}
      </Host>
    );
  }
}
